/* ====================== */
/* Vision Section         */
/* ====================== */
.vision-section {
    background: linear-gradient(rgba(0, 51, 102, 0.9), rgba(0, 51, 102, 0.9)),
      url('../images/banner.png'); /* Replace with your actual image path */
    background-position: center;
    background-size: cover;
    color: white;
    padding: 6rem 0;
    margin-bottom: 2rem;
  }
  
  .vision-section .lead {
    max-width: 700px;
    margin: 0 auto;
  }
  
  /* ====================== */
  /* Priorities Section     */
  /* ====================== */
  .priorities-section {
    padding: 4rem 0;
  }
  
  .priority-card {
    border-radius: 15px;
    overflow: hidden;
    transition: transform 0.3s ease;
    margin-bottom: 2rem;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .priority-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  }
  
  .priority-icon {
    font-size: 3rem;
    color: var(--primary-color);
    margin-bottom: 1rem;
  }
  
  .priority-card h3 {
    color: var(--primary-color);
    margin-bottom: 1rem;
  }
  
  .priority-card .lead {
    color: var(--dark-color);
    font-size: 1.1rem;
  }
  
  .priority-card ul li {
    position: relative;
    padding-left: 1.5rem;
    margin-bottom: 0.75rem;
  }
  
  .priority-card ul li::before {
    content: "âœ“";
    position: absolute;
    left: 0;
    color: var(--primary-color);
  }
  
  /* ====================== */
  /* New Banner Section     */
  /* ====================== */
  .elect-andrew-banner {
    position: relative;

    /* Replace with your own banner image file */
    background-size: cover;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .banner-overlay {
    background-color: rgba(0, 0, 0, 0.4); /* semi-transparent overlay */
    width: 100%;
    height: 100%;
  }
  
  .banner-title {
    font-size: 3rem;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 1px;
    margin-bottom: 1rem;
  }
  
  .banner-subtitle {
    font-size: 1.25rem;
    color: #f1f1f1;
    max-width: 600px;
    margin: 0 auto;
    font-style: italic;
  }
  
  /* Optional: Make outline buttons white text with transparent background */
  .btn-outline-light {
    color: #fff;
    border-color: #fff;
  }
  .btn-outline-light:hover {
    background-color: #fff;
    color: var(--primary-color);
  }
  
  /* ====================== */
  /* Responsive Design      */
  /* ====================== */
  @media (max-width: 768px) {
    .vision-section {
      padding: 4rem 1rem;
    }
  
    .priorities-section {
      padding: 2rem 1rem;
    }
  
    .priority-card {
      margin-bottom: 1.5rem;
    }
  
    .elect-andrew-banner {
      height: 40vh;
    }
  
    .banner-title {
      font-size: 2rem;
    }
  
    .banner-subtitle {
      font-size: 1rem;
    }
  
    .btn-lg {
      width: 100%;
      margin-bottom: 1rem;
      margin-right: 0 !important;
    }
  }
  